<template>
	<div class="biaozhu danwenjian">
		<!--头部-->
		<div class="header ny" style="">
		    <div class="am-container">
		        <div class="fr btn-group" style="height: 100%;line-height: 100px">
		           <Login biaoshi="cihuishaixuan"/>
		        </div>
		    </div>
		</div>
		<!--头部结束-->
		<div class="yc-box">
		    <div class="am-container">
		        <div class="banner">
		            <div class="img-title"></div>
		            <div class="fr inter">
						<interBtn style="padding-top: 46px;" title="法规标注" id="fenci"/>
					</div>
		        </div>
		        
		    </div>
		</div>

		 <div class="fc-box">
		 	<div class="check-form">
		        <div class="my-tabs" id="doc-my-tabs">
		            <div class="my-tabs-bd">
		            	<div v-if="!(faguiList.length>0)" style="text-align: center;line-height: 100px;font-size: 30px;"><i class="el-icon-loading"></i> 加载中......</div>
		            	<el-row v-else>
						  <el-col :span="12">
						  	<el-collapse v-if="faguiList.length>0" v-model="activeName" accordion>
							  <el-collapse-item v-for="(listItem,index) in faguiList" :name="listItem.icon">
							  	 <template slot="title">
							  	 	  <div class="title" :title="listItem.icon">{{listItem.icon}}</div>
								     
								 </template>
							  	<!-- <div style="position: absolute;top:0;right: 0px;"></div> -->
							    <p class="item-b">{{listItem.desc}} &nbsp;&nbsp;|&nbsp;&nbsp; {{listItem.time}}</p>
							    <div class="content">
							    	<div v-if="faguiXq=='null'" style="margin-top:10px;text-align: center;"><i class="el-icon-loading"></i> 正在加载内容</div>
							    	<div v-else v-html="faguiXq"></div>
							    </div>
							  </el-collapse-item>
							</el-collapse>
				            <div class="block">
							    <el-pagination
							      @current-change="handleCurrentChange()"
							      :current-page.sync="currentPage"
							      :page-size="10"
							      background
							      layout="prev, pager, next, jumper"
							      :total="10*pages">
							    </el-pagination>
							</div>
						  </el-col>
						  <el-col :span="12" class="words">
						  	<div class="inputs">
						  		<ul v-if="faguiList.length>0">
						  			<li class="input-item" v-for="(listItem,index) in faguiList">
						  				<textarea :id="'box'+index" class="wordInput" type="text"  :name="listItem.icon" placeholder="标签...(多个标签用,隔开)">
						  				</textarea>
						  			</li>
						  			<li>
						  				<div class="btn-box">
						  					<button type="button" class="submitBtn am-btn am-btn-primary am-active">立即提交</button>
											<button type="button" class="clearBtn am-btn am-btn-default am-active">清空</button>
						  				</div>
						  			</li>
						  		</ul>
						  	</div>
						  	<ul class="wordBox">
						  	</ul>
						  </el-col>
						</el-row>
		            	
		            </div>
		        </div>
			</div>
        </div>
	</div>
</template>
<script type="text/javascript">
	import Login from './../login'
	import interBtn from './interactions'
	import reqData from "./../../reqData.js"
	import amazeui from 'amazeui';
	require('amazeui/dist/css/amazeui.css');
	require('./image/commen.css');
	// import './image/jquery.easyui.min.js'
	export default{
		name:'biaoshi',
		data(){
			return {
				currentPage: 1,
				pages:0,
				faguiList:[],
				faguiXq:{},
				activeName: '1'
			}
		},
		components:{
			Login,
			interBtn
		},
		methods:{
			getListPages(){
		    	reqData
			        .req({
			          apiName: "listPages",
			          data: {             
			            name:','
			          }
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            this.pages=Number(data);
			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
    	
	    	},
	    	handleCurrentChange(val){
	    		$('.inputs .input-item .wordInput').val('');
	    		reqData
			        .req({
			          apiName: "faguiList2",
			          data: {             
			            num:this.currentPage,
			            name:','
			          }
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            this.faguiList=data;
			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
	    	},
	    	getFaguiXq(title){
	    		this.faguiXq='null';
	    		reqData
			        .req({
			          apiName: "faguiXq",
			          data: {             
			            ID:1,
			            icon_name:title
			          }
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            let cont=data[0].text;
			    		let reg1=new RegExp("。","g");
			    		let reg3=new RegExp(" ","g");
				    	cont=cont.replace(reg1,"。</p><p>");
						cont=cont.replace(reg3,"</p><p>");
						cont="<p>"+cont+"</p>";
			            this.faguiXq=cont;
			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
	    	}
		},
		computed:{
			userName(){
				return sessionStorage.getItem('username');
			}
		},
		watch: {
		    activeName: function (val){
		    	if(val){
		    		this.getFaguiXq(val)
		    	}
		    }
		},
		mounted: function(){
		    this.getListPages();
    		this.handleCurrentChange(1);
    		reqData
		        .req({
		          apiName: "fgBiaozhu",
		        })
		        .then(res => {
		          res = res.data;
		          if (res && res.code === "200") {
		            let data = res.data;
		            console.log(data);
		            data.forEach((value)=>{
						$('.wordBox').append('<li class="item dragitem">'+value+'</li>');
		            });
					drag();
		          }
		        })
		        .catch(err => {
		          console.log(err);
		       });
    		//        拖拽事件
    		 function drag(){
    		 	$('.dragitem').draggable({
	                revert:true,
	                proxy:'clone',
	                onStartDrag:function(){
	                	console.log("aaaaaaaaaa");
	                    $(this).draggable('options').cursor = 'not-allowed';
	                    $(this).draggable('proxy').css('z-index',999);
	                },
	                onStopDrag:function(){
	                    $(this).draggable('options').cursor='move';
	                }
	            });
	            $('.wordInput').droppable({
	                onDragEnter:function(e,source){
	                    $(source).draggable('options').cursor='auto';
	                },
	                onDragLeave:function(e,source){
	                    $(source).draggable('options').cursor='not-allowed';
	                },
	                onDrop:function(e,source){
	                	var id=$(this)[0].id;
	                    var name = $(source).html();
	                    console.log(name+'===='+id);
	                    addItem(name,id);
	                }
	            });
	            function addItem(name,id) {
	                var text=$('#'+id).val();
	                $('#'+id).val(text+name+',')
	            }
	            $('.btn-box .submitBtn').click(function(){
	    			console.log('aa');
	    			var inputs=$('.inputs .input-item');
	    			var datas=[];
	    			for(var i=0;i<inputs.length;i++){
	    				var icon=$('.wordInput',inputs[i])[0].name;
	    				var cihui=$('.wordInput',inputs[i]).val()?$('.wordInput',inputs[i]).val():',';
	    				var reg=new RegExp("，","g");
	    				cihui=cihui.replace(reg,",");
	    				if(cihui.search(",")<0){
                			cihui=cihui+","
                		};
	    				datas.push({
	    					icon:icon,
	    					cihui:cihui
	    				})
	    			};
	    			console.log(datas);
	    			reqData
				        .req({
				          apiName: "biaozhuSubmit",
				          data:{
				          	data:JSON.stringify(datas)
				          }
				        })
				        .then(res => {
				          res = res.data;
				          console.log(res.message);
				          if(res.message==1){
				          	alert("提交成功");
				          	// $('.inputs .input-item .wordInput').val('');
				          }else{
				          	alert("提交失败")
				          }
				        })
				        .catch(err => {
				          console.log(err);
				       });
	    		});
	    		$('.btn-box .clearBtn').click(function(){
	    			 $('.inputs .input-item .wordInput').val('');
	    		})
    		 }
    		
    		

		}
	}
</script>
<style>
	/*此行类别CSS*/
	.biaozhu .yc-box {
	    width: 100%;
	    height: 140px;
	    /*line-height: 85px;*/
	    background-color: #fff;
	    box-shadow: 0px 5px 6px 2px rgba( 0, 0, 0 ,0.1);
	}
    .biaozhu .banner{
        width: 100%;
        height: 140px;
        /*line-height: 140px;*/
        text-align: center;
    }
    .biaozhu .banner .img-title{
        display: inline-block;
        width: 394px;
        height: 85px;
        margin-left: 20%;
        background: url(image/title3.png) no-repeat;
        background-position: 0 90%;
        margin-top: 28px;
    }
    .biaozhu .am-container{
        max-width: 1500px;
        box-sizing: border-box;
    }
     



  
	
	.biaozhu .fc-box{
		overflow: hidden;
		/*width: 80%;
		margin: 0 auto;*/
		min-height: 500px;
		padding:2%;
		border: 1px solid #ccc;
		margin-bottom: 20px;
		box-sizing: border-box;
	}
	.biaozhu .check-form .my-tabs{
        width: 100%;
        min-height: 700px;
        padding: 30px 60px;
        box-sizing: border-box;
        text-align: justify;
        /*border: 2px solid #0fb3dc;*/
        border-radius: 8px;
        background-color: #fff;
         box-shadow: 0 1px 6px rgba(0,0,0,0.4);
    }
    .biaozhu .fc-box .words{
    	position: relative;
    }
    .biaozhu .fc-box .words .inputs{
    	position: absolute;
    	top: 0;
    	left: 20px;
    	width: 220px;
    	z-index: 999;
    }
    .biaozhu .fc-box .words .inputs .btn-box{
    	text-align: center;
    }
    .biaozhu .fc-box .words .inputs .btn-box .submitBtn,
    .biaozhu .fc-box .words .inputs .btn-box .clearBtn {
	    display: inline-block;
	    margin-bottom: 0;
	    width: 40%;
	    padding: 0; 
	    vertical-align: middle;
	    font-size: 16px;
	    font-weight: normal;
	    line-height: 30px;
	    border-radius: 6px;
	    text-align: center;
	    white-space: nowrap;
	    background-image: none;
	    border: 1px solid transparent;
	}
    /* border-radius: 0; */
   /* .biaozhu .fc-box .words .inputs .btn-box .submit,
    .biaozhu .fc-box .words .inputs .btn-box .clear{
    	display: inline-block;
    	width: 40%;
    	height: 44px;
    	background-color: 
    	margin-top: 15px 2%;
    	cursor: pointer;
    }*/
   
    .biaozhu .fc-box .words .inputs li{
    	height: 46px;
    	line-height: 46px;
    	margin-bottom: 6px;
    	clear: both;
    }
    .biaozhu .fc-box .words .inputs textarea{
    	position: absolute;
    	height: 46px;
    	line-height: 30px;
    	overflow-y: auto;
    	width: 200px;
    	padding-left: 10px;
    	border-radius: 4px;
    	border: 1px solid #ccc;
    }
    .biaozhu .fc-box .wordBox{
    	position: relative;
    	max-height: 600px;
    	overflow-y: auto;
    	padding-left: 250px;
    
    }
	.biaozhu .fc-box .wordBox li.item{
		float: left;
		cursor: pointer;
	    margin: 3px 5px;
	    border-radius: 4px;
	    padding: 2px 4px;
	    box-sizing: border-box;
	    background-color: #c8deff;
	    color: #000;
	    font-size: 15px;
	}
/*//折叠面板样式*/
	.biaozhu .el-collapse-item__wrap {
	    overflow: auto;
	    height: 300px;
	    background-color: #fff;
	    padding: 20px;
	}
	.biaozhu .el-collapse-item__content{
		font-size: 14px;
	}
	.biaozhu .el-collapse-item__header {
		position: relative;
	    color: #303133;
	    padding-left: 20px;
	    border-bottom: none;
	    margin-bottom: 6px;
	    font-size: 16px;
	    font-weight: 500;
	    background-color: rgba(227,233,242,1);
	    transition: border-bottom-color .3s;
	    outline: 0;
	}
	.biaozhu .el-collapse-item__header .title{
		white-space: nowrap;
	    overflow: hidden;
	    text-overflow: ellipsis;
	}
	.biaozhu .el-collapse-item:nth-child(even) .el-collapse-item__header{
		background-color: rgba(227,233,242,0.7);
	}
	.biaozhu .block{
		text-align: center;
	}

	/*修改分页样式*/
	.biaozhu .el-pagination.is-background .el-pager li:not(.disabled).active {
	    background-color: #409eff;
	    color: #fff;
	}
	.biaozhu .el-pagination.is-background .el-pager li:not(.disabled).active:hover{
		background-color: #409eff;
	    color: #fff;
	}
	.biaozhu .el-pagination.is-background .el-pager li:not(.disabled):hover {
	    color: #409eff;
	}
	.biaozhu .el-pagination.is-background .btn-next:disabled,
	.biaozhu .el-pagination.is-background .btn-prev:disabled{
		color: #409eff;
	}
	.biaozhu .el-input__inner:hover, .el-input__inner:focus {
	    border: 1px solid #409eff;
	}
</style>


